	position: relative;
	color: #333;
	cursor: default;
  }
  
  .dropdown .arrow {
	border-color: #999 transparent transparent;
	border-style: solid;
	border-width: 5px 5px 0;
	content: " ";
	display: block;
	height: 0;
	margin-top: 0.3rem;
	position: absolute;
	left: 400px;
	top: 14px;
	width: 0;
  }
  
  .dropdown .arrow.open {
	border-color: transparent transparent #999;
	border-width: 0 5px 5px;
  }
  
  .dropdown .selected-value input{
	background: #fdfdfd url(data:image/png;base64,) no-repeat 10px center;
	line-height: 1.5;
